import React from 'react';
import { graphql } from 'gatsby';
import Layout from '@layouts';
import { Banners, SEO, Home } from '@components';
import HomeVideo from '../popups/HomeVideo';

const { HomeBanner } = Banners;
const Index = ({ data }) => (
  <Layout bannerChildren={<HomeBanner />}>
    <SEO title='HOME' />

    <Home articleData={data} />
    <HomeVideo vidURL='Home Page Vid Url Passed as Prop' />
  </Layout>
);

export const query = graphql`
  {
    allMarkdownRemark(
      filter: { frontmatter: { content_type: { eq: "article" } } }
      sort: { fields: [frontmatter___date, frontmatter___title], order: [DESC, ASC] }
    ) {
      edges {
        node {
          frontmatter {
            content_type
            slug
            title
            subject
            excerpt
            author
            photo_credit
            category
            date
            image_url {
              childImageSharp {
                fluid {
                  src
                }
              }
            }
            image_1 {
              childImageSharp {
                fluid {
                  src
                }
              }
            }
          }
        }
      }
    }
  }
`;

export default Index;
